.square {
  background: #fff;
  border-radius: 2px;
  height: 180px;
  margin: 1rem;
  position: relative;
  width: 180px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -o-transition: transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s; /* Animation */
}

.square:hover {
  cursor: pointer;
  position: fixed;
  background: #fff;
  border-radius: 2px;
  height: 180px;
  margin: 1rem;
  position: relative;
  width: 180px;
  -webkit-box-shadow: 0 3px 30px #999, 0 3px 6px #999;
          box-shadow: 0 3px 30px #999, 0 3px 6px #999;
  text-align: left;
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.square > .img-container {
  height: 94%;
  margin: 3%;
  margin-bottom: 10%;
  overflow: hidden;
  text-align: center;

}
.card > .img-container > img {
  width: 180px;
  /* height: 180px; */
}

img {
  width: 160px;
}

.square > .img-container { 
  background: -webkit-linear-gradient(326deg, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3, #dd00f3); 
  background: -o-linear-gradient(326deg, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3, #dd00f3); 
  background: linear-gradient(124deg, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3, #dd00f3);
  background-size: 1800% 1800%;
  -webkit-animation: rainbow 10s ease infinite;
  -z-animation: rainbow 10s ease infinite;
  animation: rainbow 10s ease infinite;
}

@-webkit-keyframes rainbow {
    0%{background-position:0% 82%}
    50%{background-position:100% 19%}
    100%{background-position:0% 82%}
}
@keyframes rainbow { 
    0%{background-position:0% 82%}
    50%{background-position:100% 19%}
    100%{background-position:0% 82%}
}






